import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { FiHome, FiFilm, FiSearch, FiMenu, FiX, FiSun, FiMoon, FiUser } from 'react-icons/fi';

const Navbar = ({ darkMode, toggleDarkMode }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const navigate = useNavigate();

  const categories = ['Action', 'Comedy', 'Drama', 'Thriller', 'Horror', 'Sci-Fi', 'Animation'];

  const handleSearchSubmit = (e) => {
    e.preventDefault();
    if (searchQuery.trim()) {
      setIsOpen(false); // Close mobile menu
      navigate(`/search?q=${encodeURIComponent(searchQuery)}`);
    }
  };

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  return (
    <nav className={`sticky top-0 z-50 ${darkMode ? 'bg-gray-900' : 'bg-gray-800'} text-white shadow-lg`}>
      <div className="container mx-auto px-4 py-3">
        <div className="flex justify-between items-center">
          {/* Logo */}
          <Link to="/" className="text-xl font-bold flex items-center">
            <FiFilm className="mr-2 text-red-500" size={24} />
            <span className="bg-gradient-to-r from-red-500 to-yellow-500 bg-clip-text text-transparent">
              MovieFlix
            </span>
          </Link>

          {/* Desktop Menu */}
          <div className="hidden md:flex space-x-6 items-center">
            <Link to="/" className="hover:text-red-400 flex items-center gap-1 transition duration-200">
              <FiHome size={16} />
              <span>Home</span>
            </Link>
            
            <div className="relative group">
              <button className="hover:text-red-400 flex items-center gap-1 transition duration-200">
                <FiFilm size={16} />
                <span>Categories</span>
              </button>
              <div className="absolute left-0 mt-2 w-48 rounded-md shadow-lg bg-white dark:bg-gray-800 text-gray-800 dark:text-white hidden group-hover:block z-10 overflow-hidden transition-all duration-200 transform origin-top scale-95 group-hover:scale-100">
                <div className="py-1">
                  {categories.map((category) => (
                    <Link
                      key={category}
                      to={`/category/${category.toLowerCase()}`}
                      className="block px-4 py-2 text-sm hover:bg-gray-100 dark:hover:bg-gray-700 transition duration-150"
                    >
                      {category}
                    </Link>
                  ))}
                </div>
              </div>
            </div>
            
            {/* Search Form */}
            <form onSubmit={handleSearchSubmit} className="ml-4">
              <div className="flex">
                <input
                  type="text"
                  placeholder="Search movies..."
                  className="px-3 py-1.5 rounded-l-md text-black dark:text-white dark:bg-gray-700 focus:outline-none focus:ring-1 focus:ring-red-500"
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                />
                <button
                  type="submit"
                  className="bg-red-600 px-3 py-1.5 rounded-r-md hover:bg-red-700 transition duration-200 flex items-center"
                >
                  <FiSearch size={16} />
                </button>
              </div>
            </form>
            
            {/* Dark Mode Toggle */}
            <button 
              onClick={toggleDarkMode} 
              className="ml-4 p-2 rounded-full hover:bg-gray-700 transition duration-200"
              aria-label={darkMode ? 'Switch to light mode' : 'Switch to dark mode'}
            >
              {darkMode ? <FiSun size={18} /> : <FiMoon size={18} />}
            </button>
            
            {/* Admin Link */}
            <Link 
              to="/admin" 
              className="ml-4 p-2 rounded-md hover:bg-red-600 transition duration-200 flex items-center gap-1"
            >
              <FiUser size={16} />
              <span>Admin</span>
            </Link>
          </div>

          {/* Mobile Menu Button */}
          <div className="md:hidden flex items-center gap-2">
            <button
              onClick={toggleDarkMode}
              className="p-2 rounded-full hover:bg-gray-700 transition duration-200 mr-2"
              aria-label={darkMode ? 'Switch to light mode' : 'Switch to dark mode'}
            >
              {darkMode ? <FiSun size={18} /> : <FiMoon size={18} />}
            </button>
            
            <button
              className="p-2 rounded-md focus:outline-none focus:ring-1 focus:ring-red-500"
              onClick={toggleMenu}
              aria-label="Toggle menu"
            >
              {isOpen ? <FiX size={24} /> : <FiMenu size={24} />}
            </button>
          </div>
        </div>

        {/* Mobile Menu */}
        {isOpen && (
          <div className="md:hidden mt-4 bg-gray-800 dark:bg-gray-900 rounded-md shadow-xl overflow-hidden animate-fadeIn">
            <Link to="/" className="flex items-center gap-2 px-4 py-3 hover:bg-gray-700 border-b border-gray-700">
              <FiHome size={18} />
              <span>Home</span>
            </Link>
            
            <div className="px-4 py-3 border-b border-gray-700">
              <p className="mb-2 flex items-center gap-2">
                <FiFilm size={18} />
                <span>Categories</span>
              </p>
              <div className="pl-7 space-y-3 mt-2">
                {categories.map((category) => (
                  <Link
                    key={category}
                    to={`/category/${category.toLowerCase()}`}
                    className="block hover:text-red-400 transition duration-150"
                  >
                    {category}
                  </Link>
                ))}
              </div>
            </div>
            
            <form onSubmit={handleSearchSubmit} className="px-4 py-3 border-b border-gray-700">
              <div className="flex">
                <input
                  type="text"
                  placeholder="Search movies..."
                  className="px-3 py-2 rounded-l-md text-black dark:text-white dark:bg-gray-700 flex-grow focus:outline-none"
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                />
                <button
                  type="submit"
                  className="bg-red-600 px-3 py-2 rounded-r-md hover:bg-red-700 transition duration-200"
                >
                  <FiSearch size={18} />
                </button>
              </div>
            </form>
            
            <Link to="/admin" className="flex items-center gap-2 px-4 py-3 hover:bg-gray-700">
              <FiUser size={18} />
              <span>Admin</span>
            </Link>
          </div>
        )}
      </div>
    </nav>
  );
};

export default Navbar; 