import React from 'react';
import { Link } from 'react-router-dom';
import { FiTwitter, FiInstagram, FiFacebook, FiMail, FiYoutube, FiPhone, FiMapPin, FiChevronRight } from 'react-icons/fi';

const Footer = () => {
  const currentYear = new Date().getFullYear();

  return (
    <footer className="bg-gradient-to-b from-gray-900 to-black text-white pt-12 pb-6">
      <div className="container mx-auto px-4">
        <div className="grid grid-cols-1 md:grid-cols-12 gap-8">
          {/* About */}
          <div className="md:col-span-4">
            <div className="flex items-center mb-4">
              <span className="text-2xl font-bold bg-gradient-to-r from-red-500 to-yellow-500 bg-clip-text text-transparent">
                MovieFlix
              </span>
            </div>
            <p className="text-gray-400 mb-6 leading-relaxed">
              MovieFlix is a premium streaming platform showcasing the latest movies and TV shows. Discover exceptional content with our intuitive interface designed for the best viewing experience.
            </p>
            <div className="flex space-x-4">
              <a 
                href="https://twitter.com" 
                target="_blank" 
                rel="noopener noreferrer" 
                className="bg-gray-800 p-2.5 rounded-full hover:bg-red-600 transition-colors duration-300"
                aria-label="Twitter"
              >
                <FiTwitter size={18} />
              </a>
              <a 
                href="https://facebook.com" 
                target="_blank" 
                rel="noopener noreferrer" 
                className="bg-gray-800 p-2.5 rounded-full hover:bg-red-600 transition-colors duration-300"
                aria-label="Facebook"
              >
                <FiFacebook size={18} />
              </a>
              <a 
                href="https://instagram.com" 
                target="_blank" 
                rel="noopener noreferrer" 
                className="bg-gray-800 p-2.5 rounded-full hover:bg-red-600 transition-colors duration-300"
                aria-label="Instagram"
              >
                <FiInstagram size={18} />
              </a>
              <a 
                href="https://youtube.com" 
                target="_blank" 
                rel="noopener noreferrer" 
                className="bg-gray-800 p-2.5 rounded-full hover:bg-red-600 transition-colors duration-300"
                aria-label="YouTube"
              >
                <FiYoutube size={18} />
              </a>
            </div>
          </div>
          
          {/* Quick Links */}
          <div className="md:col-span-2">
            <h3 className="text-lg font-bold mb-5 relative">
              <span className="relative z-10">Quick Links</span>
              <span className="absolute bottom-0 left-0 w-12 h-1 bg-red-600"></span>
            </h3>
            <ul className="space-y-3">
              <li>
                <Link to="/" className="text-gray-400 hover:text-white transition-colors duration-300 flex items-center">
                  <FiChevronRight className="mr-1 text-red-500" size={14} />
                  <span>Home</span>
                </Link>
              </li>
              <li>
                <Link to="/search" className="text-gray-400 hover:text-white transition-colors duration-300 flex items-center">
                  <FiChevronRight className="mr-1 text-red-500" size={14} />
                  <span>Search</span>
                </Link>
              </li>
              <li>
                <Link to="/movies" className="text-gray-400 hover:text-white transition-colors duration-300 flex items-center">
                  <FiChevronRight className="mr-1 text-red-500" size={14} />
                  <span>Movies</span>
                </Link>
              </li>
              <li>
                <Link to="/series" className="text-gray-400 hover:text-white transition-colors duration-300 flex items-center">
                  <FiChevronRight className="mr-1 text-red-500" size={14} />
                  <span>TV Series</span>
                </Link>
              </li>
            </ul>
          </div>
          
          {/* Categories */}
          <div className="md:col-span-3">
            <h3 className="text-lg font-bold mb-5 relative">
              <span className="relative z-10">Categories</span>
              <span className="absolute bottom-0 left-0 w-12 h-1 bg-red-600"></span>
            </h3>
            <div className="grid grid-cols-2 gap-y-3">
              <Link to="/category/action" className="text-gray-400 hover:text-white transition-colors duration-300 flex items-center">
                <FiChevronRight className="mr-1 text-red-500" size={14} />
                <span>Action</span>
              </Link>
              <Link to="/category/comedy" className="text-gray-400 hover:text-white transition-colors duration-300 flex items-center">
                <FiChevronRight className="mr-1 text-red-500" size={14} />
                <span>Comedy</span>
              </Link>
              <Link to="/category/drama" className="text-gray-400 hover:text-white transition-colors duration-300 flex items-center">
                <FiChevronRight className="mr-1 text-red-500" size={14} />
                <span>Drama</span>
              </Link>
              <Link to="/category/thriller" className="text-gray-400 hover:text-white transition-colors duration-300 flex items-center">
                <FiChevronRight className="mr-1 text-red-500" size={14} />
                <span>Thriller</span>
              </Link>
              <Link to="/category/horror" className="text-gray-400 hover:text-white transition-colors duration-300 flex items-center">
                <FiChevronRight className="mr-1 text-red-500" size={14} />
                <span>Horror</span>
              </Link>
              <Link to="/category/sci-fi" className="text-gray-400 hover:text-white transition-colors duration-300 flex items-center">
                <FiChevronRight className="mr-1 text-red-500" size={14} />
                <span>Sci-Fi</span>
              </Link>
            </div>
          </div>
          
          {/* Contact */}
          <div className="md:col-span-3">
            <h3 className="text-lg font-bold mb-5 relative">
              <span className="relative z-10">Contact Us</span>
              <span className="absolute bottom-0 left-0 w-12 h-1 bg-red-600"></span>
            </h3>
            <ul className="space-y-4">
              <li className="flex items-start">
                <FiMapPin className="mt-1 mr-3 text-red-500" />
                <span className="text-gray-400">123 Streaming Avenue, Digital City, DC 45678</span>
              </li>
              <li className="flex items-center">
                <FiPhone className="mr-3 text-red-500" />
                <span className="text-gray-400">+1 (123) 456-7890</span>
              </li>
              <li className="flex items-center">
                <FiMail className="mr-3 text-red-500" />
                <span className="text-gray-400">support@movieflix.com</span>
              </li>
            </ul>
          </div>
        </div>
        
        <div className="border-t border-gray-800 mt-10 pt-6">
          <div className="flex flex-col md:flex-row md:justify-between items-center text-gray-400 text-sm">
            <p>&copy; {currentYear} MovieFlix. All rights reserved.</p>
            <div className="mt-4 md:mt-0 flex flex-wrap justify-center gap-4">
              <Link to="/terms" className="hover:text-white transition-colors duration-300">Terms of Service</Link>
              <Link to="/privacy" className="hover:text-white transition-colors duration-300">Privacy Policy</Link>
              <Link to="/faq" className="hover:text-white transition-colors duration-300">FAQ</Link>
              <Link to="/help" className="hover:text-white transition-colors duration-300">Help Center</Link>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer; 